<template>
  <v-container ref="boxIds" class="activity_box container" app>
    <div class="page activity_list">
      <template v-if="!skeleton">
        <div class="activity_list_item" v-for="(item, i) in activityList" :key="i" @click="onDetail(item)">
          <div class="activity_item column_flex" style="align-items: flex-start">
            <img v-if="item.img_src" class="activity_item_img" :src="item.img_src" alt="" />
            <space-ui v-else style="width: 100%" sign="activity_img" />
            <div class="column_title" v-if="item.mobile_title">{{ item.mobile_title }}</div>
          </div>
        </div>
      </template>
      <template v-else>
        <space-ui style="width: 100%" sign="activity_list" />
      </template>
    </div>
  </v-container>
</template>

<script>
import { mapState } from 'vuex'
import ContainerMixin from '@/components/containerMixin'
import spaceUi from '@/components/skeleton/space-ui.vue'

export default {
  name: 'activity',
  components: {
    spaceUi
  },
  mixins: [ContainerMixin],
  data() {
    return {
      skeleton: false, // 骨架屏显示
      activityList: [] // 活动列表
    }
  },
  computed: {
    ...mapState('base', ['user', 'languageType', 'isApp']),
    ...mapState('source', ['rechargeInfo'])
  },
  watch: {
    rechargeInfo: {
      handler(n = {}) {
        this.skeleton = true
        const list = Object.keys(n).map(v => ({ ...n[v], img_src: '' }))
        this.activityList = list.filter(v => v.switch == 1).sort((a, b) => (b.page_sort > a.page_sort ? 1 : -1))
        this.skeleton = false
        this.onLoad()
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    // 加载图片
    onLoad() {
      this.activityList.forEach(async item => {
        const res = await this.$u.isImgLoad(item.entrance_img).catch(e => e)
        if (res) item.img_src = item.entrance_img
      })
    },
    // 跳转详情
    onDetail(item = {}) {
      const { act_id = 0 } = item
      if (!act_id) return

      this.$router.push({ name: `activityDetail`, query: { act_id } })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/activity.modules.scss';

// app样式
.activity_box.container {
  padding: 0;
  .page.activity_list {
    padding: $activity_list_app_padding;
  }
  .activity_list_item {
    width: $activity_list_item_app_width;
    margin: $activity_list_item_app_margin;
    .activity_item {
      width: $activity_item_width;
      .activity_item_img {
        width: 100%;
        border-radius: $activity_item_img_radius;
      }
      .column_title {
        color: $activity_item_column_title_color;
        font-size: $activity_item_column_title_size;
        font-weight: $activity_item_column_title_bold;
        margin: $activity_item_column_title_margin;
      }
    }
  }
}

// pc样式
@include breakpoint-only-up() {
  .activity_box.container {
    .page.activity_list {
      margin: $activity_list_pc_margin;
      @include setBox(row, flex-start, center);
      flex-wrap: wrap;
      .activity_list_item {
        width: $activity_list_item_pc_width;
        margin: $activity_list_item_pc_margin;
      }
    }
  }
}
</style>
